import "./App.css";
import About from "./Components/About";
import Contact from "./Components/Contact";
import Navbar from "./Components/Navbar";
import TextForm from "./Components/TextForm";
import React, { useState } from "react";
import Alert from "./Components/Alert";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  const [mode, setMode] = useState("light"); //Mode Changer
  const [alert, setAlert] = useState(null);

  const displayAlert = (message, type) => {
    setAlert({
      mssg: message,
      type: type,
    });
    // Will dissolve after 1.2 second
    setTimeout(() => {
      setAlert(null);
    }, 1200);
  };

  const milkyWhite = () => {
    if (mode === "light" || mode === "dark") {
      setMode("light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";
    }
  };
  const superBlue = () => {
    if (mode === "light" || mode === "dark") {
      setMode("dark");
      document.body.style.backgroundColor = "#00193f";
      document.body.style.color = "cyan";
    }
  };
  const everGreen = () => {
    if (mode === "light" || mode === "dark") {
      setMode("dark");
      document.body.style.backgroundColor = "#002e18";
      document.body.style.color = "lime";
    }
  };

  const toggleMode = () => {
    if (mode === "light" || mode === "dark") {
      setMode("dark");
      document.body.style.backgroundColor = "#333333";
      document.body.style.color = "white";
      displayAlert("Dark Mode has been enabled", "success");
    } else {
      setMode("light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";
      displayAlert("Light Mode has been enabled", "success");
    }
  };

  return (
    <>
      <Router>
        <Navbar
          title="TEXT FACTOR"
          aboutText="About TF"
          mode={mode}
          milkyWhite={milkyWhite}
          superBlue={superBlue}
          everGreen={everGreen}
          toggleMode={toggleMode}
        />
        <Alert alert={alert} />
        {
          <div className="container my-5">
            <Switch>
              <Route exact path="/about">
                <About mode={mode} />
              </Route>
              <Route exact path="/">
                <TextForm
                  displayAlert={displayAlert}
                  heading="Enter Text Below"
                  mode={mode}
                />
              </Route>
              <Route exact path="/contact">
                <Contact title="TEXT FACTOR" mode={mode} />
              </Route>
            </Switch>
          </div>
        }
      </Router>
    </>
  );
}

export default App;
