import React, { useState } from "react";

export default function TextForm(props) {
  //Transforms Text to All-Caps
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    props.displayAlert("Transformed to ALL-CAPS", "success");
  };
  //Transforms Text to All-Lower
  const handleLowClick = () => {
    let newText = text.toLowerCase();
    setText(newText);
    props.displayAlert("Transformed to all-lower", "success");
  };
  //Clears all Text
  const handleClearClick = () => {
    let newText = "";
    setText(newText);
    props.displayAlert("Cleared all Text", "success");
  };
  //Copies all Text to Clipboard
  const handleCopy = () => {
    navigator.clipboard.writeText(text);
    props.displayAlert("Copied to Clipboard", "success");
  };
  //Removes additional Spacings
  const handleExtraSpaces = () => {
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
    props.displayAlert("Removed all additional spaces", "success");
  };
  const handleOnChange = (event) => {
    setText(event.target.value);
  };
  const [text, setText] = useState("Click on a button you desire");
  return (
    <>
      <div className="container py-3">
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <textarea
            className="form-control"
            value={text}
            onChange={handleOnChange}
            id="myBox"
            rows="8"
            style={{
              backgroundColor: props.mode === "light" ? "white" : "#333",
              color: props.mode === "light" ? "black" : "white",
            }}
          ></textarea>
        </div>
        <div id="buttons">
          <button onClick={handleUpClick} className="btn btn-primary m-2 ">
            Convert To UpperCase
          </button>
          <button onClick={handleLowClick} className="btn btn-primary m-2">
            Convert To LowerCase
          </button>
          <button onClick={handleClearClick} className="btn btn-primary m-2">
            Clear Text
          </button>
          <button onClick={handleCopy} className="btn btn-primary m-2">
            Copy Preview
          </button>
          <button onClick={handleExtraSpaces} className="btn btn-primary m-2">
            Clear Extra Spaces
          </button>
        </div>
      </div>
      <div className="container my-4">
        <h2>Text Summary</h2>
        <p>
          Number of Characters:<b> {text.length}</b>
        </p>
        <p>
          Number of Words:{" "}
          <b>
            {
              text.split(/\s+/).filter((element) => {
                return element.length !== 0;
              }).length
            }
          </b>
        </p>
        <h2>Live Preview</h2>
        <p id="preview-box">{text}</p>
      </div>
    </>
  );
}
