import React from "react";
import MyPicture from "./Assets/MyPicture.webp";

const Contact = (props) => {
  let myStyle = {
    color: props.mode === "dark" ? "white" : "black",
    backgroundColor: props.mode === "dark" ? "black" : "white",
  };
  return (
    <>
      <div className="card" width="18rem" style={myStyle}>
        <img src={MyPicture} className="card-img-top" alt="" />
        <div className="card-body">
          <h5 className="card-title">Namaste, I'm Kishan</h5>
          <p className="card-text">
            {props.title} was made using React.js and this is my first project
            made using React. Following are the things I learnt while making -
            {props.title}.
          </p>
        </div>
        <ul className="list-group list-group-flush">
          <li className="list-group-item" style={myStyle}>
            Import and Export{" "}
          </li>
          <li className="list-group-item" style={myStyle}>
            Props and useState{" "}
          </li>
          <li className="list-group-item" style={myStyle}>
            React Router and Higher-order Functions
          </li>
        </ul>
        <div className="card-body">
          <a
            href="https://github.com/CloserToTheCosmos?tab=repositories"
            target="_blank"
            rel="noreferrer"
            className="card-link"
          >
            Click here to have a look at my Repository on GitHub
          </a>
          <a
            href="https://codepen.io/Kishan_R"
            target="_blank"
            rel="noreferrer"
            className="card-link"
          >
            Click here to have a look at my CodePen
          </a>
        </div>
      </div>
    </>
  );
};

export default Contact;
